export const finish = {
  //整理规格数据
  finishGoods: function(goods) {
    // let new_goods_spec = {};
    let option1_list = [];
    let option2_list = [];
    let option3_list = [];
    let option1_key_list = [];
    let option2_key_list = [];
    let option3_key_list = [];
    if (goods) {
      goods.map(item => {
        if (item.name !== "" && item.name !== null) {
          if (option1_key_list.indexOf(item.name) < 0) {
            option1_key_list.push(item.name);
            option1_list.push(item);
          }
        }
        if (item.option1 !== "" && item.option1 !== null) {
          if (option2_key_list.indexOf(item.option1) < 0) {
            option2_key_list.push(item.option1);
            option2_list.push(item);
          }
        }
        if (item.option2 !== "" && item.option2 !== null) {
          if (option3_key_list.indexOf(item.option2) < 0) {
            option3_key_list.push(item.option2);
            option3_list.push(item);
          }
        }
      });
    }
    let new_goods_specc = {
      goods,
      option1_list,
      option1_key_list,
      option2_list,
      option2_key_list,
      option3_list,
      option3_key_list
    };
    return new_goods_specc;
  }
};
