            <span class="text">常见问题</span>
          </div>
          <div class="list">
            <div class="item">
              <p class="qu">
                <i class="u-icon u-detail-redNum"></i
                ><span>购买运费如何收取？</span>
              </p>
              <p class="an">
                单笔订单金额（不含运费）满88元免邮费；不满88元，每单收取10元运费。<br />(港澳台地区需满500元免邮费；不满500元，每单收取30元运费)
              </p>
            </div>
            <div class="item">
              <p class="qu">
                <i class="u-icon u-detail-redNum"></i
                ><span>网易严选的订单如何配送？</span>
              </p>
              <p class="an">
                网易严选会根据商品所在地、顾客所在地和商品的尺寸重量优选物流配送商，确保优质用户体验。目前暂不支持自选快递，具体物流信息可在下单成功后“我的订单-追踪物流”中查看。
              </p>
            </div>
            <div class="item">
              <p class="qu">
                <i class="u-icon u-detail-redNum"></i
