              </p>
              <p class="an">
                1.支持无忧退换货的商品，自收到商品之日起在无忧退换货期限内，顾客可申请无忧退换货；食品、定作类商品、贴身衣物、积分兑换等特殊商品，无质量问题不支持退换货；<br />2.
                如果是退货，退款将原路返还，不同的银行处理时间不同，预计1-5个工作日到账；<br />3.退货流程：<br />确认收货-申请退货-客服审核通过-用户寄回商品-仓库签收验货-退款审核-退款完成；<br />4.换货流程：<br />确认收货-申请换货-客服审核通过-用户寄回商品-仓库签收验货-客服确认-换货完成；<br />5.退换货运费由网易严选承担（大件商品除外）。上门取件费用由网易严选统一与快递服务商结算，顾客只需将退换货商品交给上门取件快递员寄回。如顾客选择自行寄回商品，请先垫付运费，到货验证商品后，严选将以现金券形式为用户报销运费，不接受单方面到付件。
              </p>
            </div>
            <div class="item">
              <p class="qu">
                <i class="u-icon u-detail-redNum"></i>
                <span>食品类商品什么情况下支持退换货？</span>
